<template>
  <div class="rightContent">
    <div class="navTile">
      当前位置：
      <router-link :to="{name:'Main'}">首页</router-link>
      &nbsp;>>&nbsp;
      <router-link :to="{name:'Material',query:{typeName,index:0}}">教学大纲</router-link>
      &nbsp;>>&nbsp;
      <router-link :to="{name:'MaterialDetail',query:{typeName,index}}">{{menuName}}</router-link>
    </div>
    <div class="content">
      <div class="myDetail">
        <div id="introduce" class="introduce">
          {{setIntroduce('introduce',file.content)}}
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import merge from 'webpack-merge'               //用于动态修改地址栏参数的
  import {mapActions} from 'vuex'
  export default {
    name: '',
    data () {
      return {
        List:[],                 //评价数据数组
        menus:[],                //目录数组
        menuName:'',             //菜单名
        typeName:'Material',     //当前菜单栏选项
        index:0,                 //第几个小菜单

        file:{},                 //当前显示评价
      }
    },
    methods: {
      ...mapActions(['choiceMenuId','choiceTypeMenuId']),

      //显示介绍
      setIntroduce(id,videoIntro){
        if(videoIntro){
          $('#'+id).html(videoIntro)
        }else{
          $('#'+id).html('此栏目下没有数据')
        }
      },

      //修改路由参数
      chanceUrlQuery(data){
        //清空所有的参数：
        this.$router.push({
          query:merge({},{})
        })
        //更新路由参数
        this.$router.push({
          query:merge(this.$route.query,data)
        })
      },

      //更新数据显示
      handleId(){
        var {List,menus,typeName,index} = this
        this.file = List[index]
        this.menuName = menus[index].menuName
        this.choiceMenuId(typeName)
        this.choiceTypeMenuId(typeName+index)
      },
    },
    watch: {
      //深度监听地址栏参数变化，一变化就切换对应的数据
      $route: {
        handler() {
          this.index = this.$route.query.index * 1
          this.handleId()
        },
        deep: true,
      }
    },
    mounted () {
      //页面重新加载时获取地址栏参数显示对应数据
      if(this.$route.query.typeName){
        this.typeName = this.$route.query.typeName
        this.index = this.$route.query.index * 1
      }else{
        var {typeName,index} = this
        this.chanceUrlQuery({typeName,index})
      }
      //当前栏目下的子菜单
      this.menus = [
        {
          typeName:'Material',
          menuName:'教学大纲',
          name:'MaterialOutLine'
        },
        {
          typeName:'Material',
          menuName:'教学目标',
          name:'MaterialDetail'
        },
        {
          typeName:'Material',
          menuName:'教学内容组织形式',
          name:'MaterialForm'
        },
      ],      //目录
      //当前栏目下的各个菜单包含的数据
      this.List = [
        {
          "id": "0",
          "courseName": "教学大纲",
          "content":'',
        },
        {
          "id": "1",
          "courseName": "教学目标",
          "content":'本课程要求学生掌握家畜育种学的基本概念和原理，包括数量性状选择原理，育种值估计原理，近交与远交的遗传效应及用途，杂种优势产生的原理和利用，品种与品系培育方法。要求学生熟悉各种常见家畜品种的特征和生产力类型，熟练掌握系谱编制、综合选择指数制订、近交系数与亲缘系数制订、个体选配等必备的家畜育种措施；能根据实际生产需求设计育种方案并确定相关的育种措施。通过本课程的学习，使学生具备运用育种理论和措施解决生产中实际问题的能力。',
        },
        {
          "id": "2",
          "courseName": "教学内容组织形式",
          "content":'',
        },
      ]
      //初始化数据
      this.handleId()
    },
  }
</script>

<style lang="less">
  .userResource{
    .rightContent{
      .navTile{
        padding-left: 40px;
        height: 45px;
        border-radius: 5px 5px 0 0;
        line-height: 45px;
        color: #fff;
        font-size: 14px;
        background: rgb(85,171,235);
        overflow: hidden;
        a{
          color: white;
          text-decoration: none;
          &:hover{
            text-decoration: underline;
          }
        }
      }
      .content{
        line-height: 24px;
        padding: 10px 15px 15px;
        border: 1px solid #CECECE;
        border-top: 0;
        .myDetail{
          min-height: 470px;
          .introduce{
            color: #333;
            text-indent: 2em;
            font-size: 14px;
            line-height: 23px;
            min-height: 100px;
          }
        }
      }
    }
  }
  @media screen and (max-width: 767px){
    .userResource{
      .rightContent{
        .navTile{
          padding-left: 10px;
        }
      }
    }
  }
</style>



